.page {
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    background-color: $colorBody;
    height: 100vh;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateY(0);
    }
  }
}
